<template>
  <div class="menu">
    <!---->
    <div style="display: flex;flex-direction: row">
      <el-avatar shape="square" :src="music.avatar"></el-avatar>
      <div style="display: flex;flex-direction: column;margin-left: 10px;font-size: 14px">
        <span style="letter-spacing: normal">{{ music.name }}</span>
        <span style="font-weight: normal;letter-spacing: normal">{{ music.author }} - {{ music.album }}</span>
      </div>
    </div>
    <hr class="hr">
    <!---->
    <template v-if="mode==='star'">
      <div class="item" @mouseenter="hideSecond" @click="play">
        <i class="el-icon-caret-right icon"></i>
        <span></span>
      </div>
      <div class="item" @mouseenter="hideSecond" @click="nextPlay">
        <i class="el-icon-d-arrow-right icon"></i>
        <span></span>
      </div>
      <hr class="hr" @mouseenter="hideSecond">
      <div class="item" @mouseenter="showSecond">
        <i class="el-icon-star-off icon"></i>
        <span></span>
        <span style="float: right"> <i class="el-icon-arrow-right" style="font-weight: bold"></i> </span>
        <div class="secondMenu" @mouseenter="showSecond">
          <div class="listItem" v-for="list in lists" @click="star(list)">
            {{ list.title }}
          </div>
        </div>
      </div>
      <div class="item" @mouseenter="hideSecond" @click="copy">
        <i class="el-icon-copy-document icon"></i>
        <span></span>
      </div>
      <div class="item" @mouseenter="hideSecond" @click="download">
        <i class="el-icon-download icon"></i>
        <span></span>
      </div>
    </template>
    <template v-if="mode==='search'">
      <div class="item" @mouseenter="hideSecond" @click="play">
        <i class="el-icon-caret-right icon"></i>
        <span></span>
      </div>
      <div class="item" @mouseenter="hideSecond" @click="nextPlay">
        <i class="el-icon-d-arrow-right icon"></i>
        <span></span>
      </div>
      <hr class="hr" @mouseenter="hideSecond">
      <div class="item" @mouseenter="showSecond">
        <i class="el-icon-star-off icon"></i>
        <span></span>
        <span style="float: right"> <i class="el-icon-arrow-right" style="font-weight: bold"></i> </span>
        <div class="secondMenu" @mouseenter="showSecond">
          <div v-if="!lists.length" style="text-align: center">
            
          </div>
          <div v-else class="listItem" v-for="list in lists" @click="star(list)">
            {{ list.title }}
          </div>
        </div>
      </div>
      <div class="item" @mouseenter="hideSecond" @click="copy">
        <i class="el-icon-copy-document icon"></i>
        <span></span>
      </div>
      <div class="item" @mouseenter="hideSecond" @click="download">
        <i class="el-icon-download icon"></i>
        <span></span>
      </div>
    </template>
    <template v-else-if="mode==='list'">
      <div class="item" @click="play">
        <i class="el-icon-caret-right icon"></i>
        <span></span>
      </div>
      <div class="item" @click="nextPlay">
        <i class="el-icon-d-arrow-right icon"></i>
        <span></span>
      </div>
      <hr class="hr">
      <div class="item" @click="deleteStar">
        <i class="el-icon-delete icon"></i>
        <span></span>
      </div>
      <div class="item" @click="copy">
        <i class="el-icon-copy-document icon"></i>
        <span></span>
      </div>
      <div class="item" @click="download">
        <i class="el-icon-download icon"></i>
        <span></span>
      </div>
    </template>
    <template v-else>

    </template>
  </div>
</template>
<script>
export default {
  name: "Menu",
  data() {
    return {
      //
      music: {},
      //
      lists: [],
      //searchlistmine
      mode: '',
      //
      list: {},
      //
      musics: [],
      // 
      position: [0, 0]
    }
  },
  methods: {
    showSecond() {
      this.$nextTick(() => {
        let menu = document.getElementsByClassName('secondMenu')[0]
        if (menu.style !== null) {
          menu.style.animationDuration = '0.2s'
          menu.style.animationName = 'in'
          menu.style.display = 'flex'
          console.log(window.innerWidth, window.innerHeight)
          console.log(this.position)

          if (this.position[0] > window.innerWidth - 400) {
              menu.style.left = '-160px'
          }else{
            menu.style.left = '240px'
          }
          if(this.position[1] > window.innerHeight - 450){
            menu.style.top = '25%'
          }else{
            menu.style.top = '55%'
          }

        }
      })
    },
    hideSecond() {
      this.$nextTick(() => {
        let menu = document.getElementsByClassName('secondMenu')[0]
        if (menu.style !== null) {
          menu.style.display = 'none'
          menu.style.animationDuration = '0.2s'
          menu.style.animationName = 'out'
        }
      })
    },
    //
    getLists() {
      this.request.get('/user/getLists/' + JSON.parse(localStorage.getItem('user')).id,).then(res => {
        if (res.code === '200') {
          this.lists = res.data
        } else {
          this.$notify({
            title: '',
            message: res.msg,
            type: 'error'
          })
        }
      }).catch(err => {
        this.$notify({
          title: '',
          message: err,
          type: 'error'
        })
      })
    },
    //
    star(list) {
      this.request.post('/list/saveMusic', {"listId": list.id, "musicId": this.music.id}).then(res => {
        if (res.code === '200') {
          this.$notify({
            title: '',
            message: `${this.music.name}${list.title}`,
            type: 'success'
          })
        } else {
          this.$notify({
            type: 'error',
            title: '',
            message: res.msg
          })
        }
      }).catch(err => {
        this.$notify({
          type: 'error',
          title: '',
          message: err
        })
      })
      //
      this.$bus.$emit('showMenu', 0, 0, false, {})
    },
    //
    download() {
      window.open(this.music.url)
      //
      this.$bus.$emit('showMenu', 0, 0, false, {})
    },
    //
    copy() {
      this.$copyText(this.music.name).then(() => {
        this.$notify({
          title: '',
          message: '',
          type: 'success'
        })
      }, () => {
        this.$notify({
          title: '',
          message: '',
          type: 'error'
        })
      })
      //
      this.$bus.$emit('showMenu', 0, 0, false, {})
    },
    //
    play() {
      this.$bus.$emit('play', this.music, this.musics)
      //
      this.$bus.$emit('showMenu', 0, 0, false, {})
    },
    //
    deleteStar() {
      this.request.post('/list/deleteMusic', {"listId": this.list.id, "musicId": this.music.id}).then(res => {
        if (res.code === '200') {
          this.$notify({
            title: '',
            message: `${this.music.name}${this.list.title}`,
            type: 'success'
          })
          //
          this.$bus.$emit('changeList')
        } else {
          this.$notify({
            type: 'error',
            title: '',
            message: res.msg
          })
        }
      }).catch(err => {
        this.$notify({
          type: 'error',
          title: '',
          message: err
        })
      })
      //
      this.$bus.$emit('showMenu', 0, 0, false, {})
    },
    //
    nextPlay() {
      //
      const item = localStorage.getItem('playList')
      let list = item ? JSON.parse(item) : [this.music]
      if (item) {
        //
        //
        list = list.filter(m => {
          return m.id !== this.music.id
        })
        list.splice(1, 0, this.music)
      }
      //
      localStorage.setItem('playList', JSON.stringify(list))
      //
      this.$bus.$emit('getCurrentPlayMusic')
      this.$bus.$emit('getPlayList')
      //
      this.$bus.$emit('showMenu', 0, 0, false, {})
    }
  },
  mounted() {
    this.$bus.$on('showMenu', (x, y, b, music, list, musics, mode) => {
      let container = document.querySelector('.menu')
      if (container.style !== null) {
        if (b) {
          //
          this.music = music
          this.list = list
          this.mode = mode
          this.musics = musics
          this.position[0] = x
          this.position[1] = y
          this.getLists()
          //
          if (container.style.display === 'flex') {
            // 
            container.style.display = 'none'
            container.style.animationDuration = '0.1s'
            container.style.animationName = 'out'
            setTimeout(() => {
              //
              container.style.left = x + 'px'
              container.style.top = y + 'px'
              container.style.animationName = 'in'
              container.style.display = 'flex'
            }, 100)
          } else {
            container.style.animationDuration = '0.2s'
            //
            container.style.left = x + 'px'
            container.style.top = y + 'px'
            container.style.animationName = 'in'
            container.style.display = 'flex'
          }
        } else {
          container.style.animationDuration = '0.2s'
          container.style.animationName = 'out'
          setTimeout(() => {
            container.style.display = 'none'
          }, 200)
        }
      }
    })
  },
  beforeDestroy() {
    this.$bus.$off('showMenu')
  }
}
</script>

<style scoped>
.menu {
  display: none;
  position: absolute;
  width: 240px;
  padding: 10px;
  background: var(--menuBg);
  z-index: 1;
  border-radius: 10px;
  border: 1px var(--menuBorder) solid;
  color: var(--menuText);
  backdrop-filter: blur(8px);
  flex-direction: column;
  font-size: 14px;
  font-weight: bold;
  animation: in 0.2s forwards;
  transition: 0s;
}

.item {
  padding: 8px;
  border-radius: 3px;
  font-size: 13px;
  cursor: pointer;
}

.item:hover {
  background-color: var(--menuHover);
}

.icon {
  font-size: 15px;
  margin-right: 15px;
  font-weight: bold;
}

.hr {
  width: 100%;
  border: var(--menuBorder) solid 1px;
  margin-top: 10px;
}

.secondMenu {
  position: absolute;
  width: 160px;
  /*height: 200px;*/
  /*overflow-y: scroll;*/
  background-color: var(--menuBg);
  border: solid 1px var(--menuBorder);
  left: 99%;
  top: 55%;
  border-radius: 10px;
  display: none;
  flex-direction: column;
  padding: 5px;
  backdrop-filter: blur(8px);
}

.listItem {
  padding: 10px;
  border-radius: 5px;
}

.listItem:hover {
  background-color: var(--menuHover);
}

</style>
<style>
@keyframes in {
  from {
    opacity: 0
  }
  to {
    opacity: 1
  }
}

@keyframes out {
  from {
    opacity: 1
  }
  to {
    opacity: 0
  }
}

::-webkit-scrollbar {
  overflow: auto;
  width: 10px;
}

::-webkit-scrollbar-thumb {
  background-color: var(--scrollbar);
  border-radius: 5px;
}

::-webkit-scrollbar-thumb:hover {
  background-color: var(--scrollbarHover);
}

::-webkit-scrollbar-track {
  background-color: rgba(0, 0, 0, 0);
  border-radius: 5px;
}
</style>